import React from "react";
import Carousel from "../components/Carousel";
import "./index.css"

const loveTime = (new Date().getTime() - new Date('2022-02-05 02:00:00').getTime()) / 86400000;

export default () => {
  return (
    <div className="czqxj-love-page">
      <div className="love-page-intro">
        <div className="intro-text">
          <h2>关于我们</h2>
          <p>彼此爱一生...</p>
        </div>
        <Carousel />
      </div>
      <div className="love-time">
        我们已相恋{parseInt(loveTime, 10)}天
      </div>
      <div className="love-fragrance">
        <div>
          <img src="https://sukiui.com/i/2022/09/17/uohkmb.jpg" alt="" />
        </div>
      </div>
    </div>
  );
}