import React from "react";
import { Carousel } from 'antd';
import { carouselImgs } from "../../dataSource";
import "./index.css";

export default () => {
  return (
    <div className="czq-carousel-content">
      <Carousel autoplay >
        {carouselImgs.map(item => {
          return (
            <div className="img-item" key={item.id}>
              <img src={item.img} alt="" />
            </div>
          )
        })}
      </Carousel>
    </div>
  );
}